import React from 'react'
import { useState } from 'react';

export const AddToDo = (props) => {

    // const [title, setTitle] = useState('Order Biryani');
    // const [description, setDescription] = useState('Call Chicken Point and order Hyderabadi Roasted Biryani');

    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');

    let addToDoView =
        <form className=' mx-auto my-3 card shadow rounded rounded-3 bg-transparent text-center my-auto postion-absolute'>
            <div className='card-header'>
                <input type="text" className="h4 lead text-center text-capitalize fw-bold border-0  bg-transparent" id="title" placeholder="Enter Title" value={title} onChange={(e) => { setTitle(e.target.value) }} />
            </div>

            <div className="card-body p-2  ">
                <textarea type="textarea" className="col-12 bg-transparent lead text-center border-0" id="description" placeholder="Enter Description" value={description} onChange={(e) => { setDescription(e.target.value) }} />

            </div>
            <div className="card-footer p-2  ">
                <button className='btn btn-sm btn-outline-primary mx-auto' type='submit' onClick={(e) => {

                    if (!title || !description) {
                        alert('Enter Title and Description both')
                        return
                    }

                    e.preventDefault();
                    props.onAdd({ title: title, description: description });
                    setTitle('');
                    setDescription('');
                }}>Add To Do</button>

            </div>

        </form>


    return (addToDoView)
}
