
import './App.css';
import Header from './Components/Header';
import ToDos from './Components/ToDos';
import { Footer } from './Components/Footer';
import { useEffect, useState } from 'react';
import { AddToDo } from './Components/AddToDo';


function App() {

  let todoList = [];

  if (localStorage.getItem('todos')) {
    todoList = JSON.parse(localStorage.getItem('todos'));
  }

  const [todos, updateTodos] = useState(todoList);

  const onDelete = (todo) => {
    updateTodos(todos.filter((e) => { return e !== todo }))
  }
  const onClear = () => {
    updateTodos(todos.filter((e) => { return null }))
  }
  const onReset = () => {
    updateTodos(todoList);
  }
  const onAdd = (todo) => {
    if (todo != null && todos.length > 0) {
      todo.srNo = todos[todos.length - 1].srNo + 1;
    }
    else
      todo.srNo = 1;

    updateTodos(todos.concat(todo))
  }

  useEffect(() => {
    localStorage.setItem('todos', JSON.stringify(todos))
  }, [todos])

  return (

    
      <div className="App max-height-100-vh">
        <Header title='ToDos List' onClear={onClear} onReset={onReset} />
        <div className='container'>
          
              <div className='row height-87-vh'>
                <div className='col-lg-3 col-10 mx-auto my-lg-auto my-5'>
                  <AddToDo onAdd={onAdd} />
                </div>
                <div className={"col-lg-9 col-12 mx-auto my-lg-auto" + (todos.length > 1 ? " overflow-auto toDOsDisplay  my-5" : "")}>
                  <ToDos todos={todos} onDelete={onDelete} />
                </div>
              </div>
        </div>
        <Footer visible={true} />
      </div>

  );
}

export default App;
